import React, { useState } from 'react';
import { CheckCircle, XCircle, Clock, Play, AlertTriangle, Code } from 'lucide-react';

interface TestSuite {
  id: string;
  name: string;
  platform: string;
  tests: number;
  passed: number;
  failed: number;
  pending: number;
  lastRun: string;
  status: 'passing' | 'failing' | 'pending' | 'running';
}

export default function FeatureValidation() {
  const [testSuites, setTestSuites] = useState<TestSuite[]>([
    {
      id: '1',
      name: 'SMS API Integration Tests',
      platform: 'SMS',
      tests: 45,
      passed: 43,
      failed: 2,
      pending: 0,
      lastRun: '2 hours ago',
      status: 'failing'
    },
    {
      id: '2',
      name: 'WhatsApp Message Delivery',
      platform: 'WhatsApp',
      tests: 32,
      passed: 32,
      failed: 0,
      pending: 0,
      lastRun: '1 hour ago',
      status: 'passing'
    },
    {
      id: '3',
      name: 'RCS Rich Media Tests',
      platform: 'RCS',
      tests: 28,
      passed: 26,
      failed: 0,
      pending: 2,
      lastRun: '3 hours ago',
      status: 'pending'
    },
    {
      id: '4',
      name: 'Voice Call Quality Tests',
      platform: 'Voice',
      tests: 18,
      passed: 18,
      failed: 0,
      pending: 0,
      lastRun: '30 minutes ago',
      status: 'passing'
    },
    {
      id: '5',
      name: 'Video Conferencing Tests',
      platform: 'Video',
      tests: 25,
      passed: 0,
      failed: 0,
      pending: 25,
      lastRun: 'Never',
      status: 'pending'
    }
  ]);

  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'passing':
        return <CheckCircle className="w-5 h-5 text-green-400" />;
      case 'failing':
        return <XCircle className="w-5 h-5 text-red-400" />;
      case 'pending':
        return <Clock className="w-5 h-5 text-yellow-400" />;
      case 'running':
        return <Play className="w-5 h-5 text-blue-400 animate-pulse" />;
      default:
        return <Clock className="w-5 h-5 text-slate-400" />;
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'passing': return 'bg-green-500/20 text-green-400 border-green-500/30';
      case 'failing': return 'bg-red-500/20 text-red-400 border-red-500/30';
      case 'pending': return 'bg-yellow-500/20 text-yellow-400 border-yellow-500/30';
      case 'running': return 'bg-blue-500/20 text-blue-400 border-blue-500/30';
      default: return 'bg-slate-500/20 text-slate-400 border-slate-500/30';
    }
  };

  const runSingleTest = (suiteId: string) => {
    setTestSuites(prevSuites =>
      prevSuites.map(suite => {
        if (suite.id === suiteId) {
          return {
            ...suite,
            status: 'running' as const
          };
        }
        return suite;
      })
    );

    setTimeout(() => {
      setTestSuites(prevSuites =>
        prevSuites.map(suite => {
          if (suite.id === suiteId) {
            const totalTests = suite.tests;
            const passedTests = totalTests;
            const now = new Date();
            const timeAgo = 'Just now';

            return {
              ...suite,
              passed: passedTests,
              failed: 0,
              pending: 0,
              lastRun: timeAgo,
              status: 'passing' as const
            };
          }
          return suite;
        })
      );
    }, 2000);
  };

  const runAllTests = () => {
    setTestSuites(prevSuites =>
      prevSuites.map(suite => ({
        ...suite,
        status: 'running' as const
      }))
    );

    setTimeout(() => {
      setTestSuites(prevSuites =>
        prevSuites.map(suite => {
          const totalTests = suite.tests;
          const passedTests = totalTests;

          return {
            ...suite,
            passed: passedTests,
            failed: 0,
            pending: 0,
            lastRun: 'Just now',
            status: 'passing' as const
          };
        })
      );
    }, 3000);
  };

  const totalTests = testSuites.reduce((sum, suite) => sum + suite.tests, 0);
  const totalPassed = testSuites.reduce((sum, suite) => sum + suite.passed, 0);
  const totalFailed = testSuites.reduce((sum, suite) => sum + suite.failed, 0);
  const totalPending = testSuites.reduce((sum, suite) => sum + suite.pending, 0);
  const passRate = totalTests > 0 ? ((totalPassed / totalTests) * 100).toFixed(1) : 0;

  return (
    <div className="flex-1 overflow-auto bg-slate-950 p-8">
      <div className="mb-8">
        <div className="flex items-center justify-between mb-2">
          <div>
            <h1 className="text-4xl font-bold text-white">Feature Validation</h1>
            <p className="text-slate-400 text-lg mt-2">
              Automated testing and validation for all platform features
            </p>
          </div>
          <button
            onClick={runAllTests}
            disabled={testSuites.some(s => s.status === 'running')}
            className="bg-blue-600 hover:bg-blue-700 text-white px-6 py-3 rounded-lg flex items-center gap-2 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
          >
            <Play className="w-5 h-5" />
            {testSuites.some(s => s.status === 'running') ? 'Running...' : 'Run All Tests'}
          </button>
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
        <div className="bg-gradient-to-br from-slate-800/50 to-slate-800/30 backdrop-blur-sm border border-slate-700/50 rounded-2xl p-6">
          <div className="flex items-start justify-between mb-4">
            <div>
              <p className="text-slate-400 text-sm font-medium mb-1">Total Tests</p>
              <p className="text-3xl font-bold text-white">{totalTests}</p>
            </div>
            <div className="bg-blue-500/20 p-3 rounded-xl">
              <Code className="w-6 h-6 text-blue-400" />
            </div>
          </div>
          <p className="text-slate-400 text-sm">Across all platforms</p>
        </div>

        <div className="bg-gradient-to-br from-slate-800/50 to-slate-800/30 backdrop-blur-sm border border-slate-700/50 rounded-2xl p-6">
          <div className="flex items-start justify-between mb-4">
            <div>
              <p className="text-slate-400 text-sm font-medium mb-1">Passed</p>
              <p className="text-3xl font-bold text-white">{totalPassed}</p>
            </div>
            <div className="bg-green-500/20 p-3 rounded-xl">
              <CheckCircle className="w-6 h-6 text-green-400" />
            </div>
          </div>
          <p className="text-green-400 text-sm">Success rate: {passRate}%</p>
        </div>

        <div className="bg-gradient-to-br from-slate-800/50 to-slate-800/30 backdrop-blur-sm border border-slate-700/50 rounded-2xl p-6">
          <div className="flex items-start justify-between mb-4">
            <div>
              <p className="text-slate-400 text-sm font-medium mb-1">Failed</p>
              <p className="text-3xl font-bold text-white">{totalFailed}</p>
            </div>
            <div className="bg-red-500/20 p-3 rounded-xl">
              <XCircle className="w-6 h-6 text-red-400" />
            </div>
          </div>
          <p className="text-red-400 text-sm">Requires attention</p>
        </div>

        <div className="bg-gradient-to-br from-slate-800/50 to-slate-800/30 backdrop-blur-sm border border-slate-700/50 rounded-2xl p-6">
          <div className="flex items-start justify-between mb-4">
            <div>
              <p className="text-slate-400 text-sm font-medium mb-1">Pending</p>
              <p className="text-3xl font-bold text-white">{totalPending}</p>
            </div>
            <div className="bg-yellow-500/20 p-3 rounded-xl">
              <Clock className="w-6 h-6 text-yellow-400" />
            </div>
          </div>
          <p className="text-yellow-400 text-sm">Not yet run</p>
        </div>
      </div>

      {totalFailed > 0 && (
        <div className="bg-red-500/10 border border-red-500/30 rounded-xl p-4 mb-8 flex items-start gap-3">
          <AlertTriangle className="w-6 h-6 text-red-400 flex-shrink-0" />
          <div>
            <h3 className="text-red-400 font-semibold mb-1">Test Failures Detected</h3>
            <p className="text-slate-400 text-sm">
              {totalFailed} test{totalFailed > 1 ? 's have' : ' has'} failed. Please review and fix the issues before deploying to production.
            </p>
          </div>
        </div>
      )}

      <div className="bg-slate-800/50 border border-slate-700 rounded-xl p-6">
        <h2 className="text-2xl font-bold text-white mb-6">Test Suites</h2>

        <div className="space-y-4">
          {testSuites.map((suite) => (
            <div
              key={suite.id}
              className="bg-slate-900/50 border border-slate-700 rounded-lg p-6 hover:border-slate-600 transition-colors"
            >
              <div className="flex items-start justify-between mb-4">
                <div className="flex-1">
                  <div className="flex items-center gap-3 mb-2">
                    <h3 className="text-xl font-semibold text-white">{suite.name}</h3>
                    <span className={`px-3 py-1 rounded-full text-xs font-medium border ${getStatusColor(suite.status)}`}>
                      {suite.status}
                    </span>
                  </div>
                  <div className="flex items-center gap-4 text-sm text-slate-400">
                    <span>Platform: {suite.platform}</span>
                    <span>•</span>
                    <span>Last run: {suite.lastRun}</span>
                  </div>
                </div>
                <button
                  onClick={() => runSingleTest(suite.id)}
                  disabled={suite.status === 'running'}
                  className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg flex items-center gap-2 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  <Play className="w-4 h-4" />
                  {suite.status === 'running' ? 'Running...' : 'Run Tests'}
                </button>
              </div>

              <div className="grid grid-cols-4 gap-4">
                <div className="bg-slate-800 rounded-lg p-3">
                  <div className="text-slate-400 text-xs mb-1">Total</div>
                  <div className="text-white text-2xl font-bold">{suite.tests}</div>
                </div>
                <div className="bg-green-500/10 rounded-lg p-3">
                  <div className="text-green-400 text-xs mb-1">Passed</div>
                  <div className="text-green-400 text-2xl font-bold flex items-center gap-2">
                    <CheckCircle className="w-5 h-5" />
                    {suite.passed}
                  </div>
                </div>
                <div className="bg-red-500/10 rounded-lg p-3">
                  <div className="text-red-400 text-xs mb-1">Failed</div>
                  <div className="text-red-400 text-2xl font-bold flex items-center gap-2">
                    <XCircle className="w-5 h-5" />
                    {suite.failed}
                  </div>
                </div>
                <div className="bg-yellow-500/10 rounded-lg p-3">
                  <div className="text-yellow-400 text-xs mb-1">Pending</div>
                  <div className="text-yellow-400 text-2xl font-bold flex items-center gap-2">
                    <Clock className="w-5 h-5" />
                    {suite.pending}
                  </div>
                </div>
              </div>

              {suite.tests > 0 && (
                <div className="mt-4">
                  <div className="flex justify-between text-sm mb-2">
                    <span className="text-slate-400">Progress</span>
                    <span className="text-white font-medium">
                      {((suite.passed / suite.tests) * 100).toFixed(0)}%
                    </span>
                  </div>
                  <div className="w-full bg-slate-800 rounded-full h-2">
                    <div
                      className="bg-gradient-to-r from-green-500 to-emerald-500 h-2 rounded-full transition-all"
                      style={{ width: `${(suite.passed / suite.tests) * 100}%` }}
                    />
                  </div>
                </div>
              )}
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
